* {
    font-family: Arial, Helvetica, sans-serif;
}

body {
    padding: 1rem;
}

input {
    color: grey;
    font-size: 1.3rem;
    width: 100%;
}
.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-right: 10px;
    margin-bottom: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 175px;
}

